@font-face {
  src: url("helvetica.27203c3f.woff2");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  src: url("helvetica-bold.4c13dce9.woff2");
  font-style: normal;
  font-weight: 700;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  color: inherit;
  font-size: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

li {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  color: var(--primary-text-color);
  font-family: Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --primary-text-color: #5c5c5e;
  --primary-gradient: linear-gradient(180deg, #bc10d8 0%, rgba(80, 0, 250, .93) 100%);
  --white-color: #fff;
  --white-text-color: #f6f4f5;
  --secondary-button-bg-color: #d9d9d9;
  --activ-text-color: #070707;
  --accent-color: #bc10d8;
}

.header {
  height: 80px;
  border-bottom: 1px solid #5c5c5e;
  padding-top: 19px;
  padding-bottom: 19px;
}

.nav {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.menu {
  text-transform: uppercase;
  align-items: center;
  gap: 20px;
  display: flex;
}

.logo {
  justify-self: center;
}

.basket {
  justify-self: end;
  align-items: center;
  display: flex;
  position: relative;
}

.basket--on span {
  width: 15px;
  height: 15px;
  background: var(--primary-gradient);
  color: var(--white-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  line-height: 0;
  display: flex;
  position: absolute;
  bottom: 4px;
  right: -2px;
}

.section-title {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
}

.hero {
  min-height: calc(100vh - 81px);
  background-image: url("hero-bg.78117c03.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero__title {
  padding-top: 230px;
  font-size: 96px;
  font-weight: 400;
  line-height: 1.14;
}

.hero__subtitle {
  margin-bottom: 5px;
  font-size: 36px;
  line-height: 1.14;
}

.hero__text {
  margin-bottom: 50px;
  font-size: 13px;
  line-height: 15px;
}

.hero__buy-text {
  padding-top: 6px;
  font-size: 13px;
  line-height: 15px;
}

.buy-button {
  cursor: pointer;
  background: var(--primary-gradient);
  text-transform: uppercase;
  border: none;
  padding: 1px;
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  display: flex;
}

.buy-button__text {
  color: var(--white-text-color);
  text-transform: uppercase;
  padding: 20px 110px;
}

.buy-button__price {
  color: var(--primary-text-color);
  background-color: var(--secondary-button-bg-color);
  padding: 20px 105px;
}

.product-features {
  padding: 100px 0;
}

.product-features__titles {
  display: flex;
}

.product-features__title {
  flex-basis: 300px;
  padding-right: 10px;
}

.product-features__slogan {
  max-width: 330px;
  text-transform: uppercase;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 2px;
  font-size: 36px;
  line-height: 1.15;
}

.content-container {
  grid-template-columns: 300px auto;
  display: grid;
}

.product-features__items {
  flex-direction: column;
  align-items: start;
  display: flex;
}

.product-features__item-button {
  margin-bottom: 20px;
  font-weight: 700;
}

.product-features__item-button--active {
  color: var(--activ-text-color);
}

.product-features__content {
  min-height: 320px;
  position: relative;
}

.product-features__item-content {
  grid-gap: 40px;
  opacity: 0;
  grid-template-rows: auto 150px;
  grid-template-columns: auto 280px;
  transition: opacity .5s;
  display: grid;
  position: absolute;
}

.product-features__item-content--active {
  opacity: 1;
}

.product-features__image {
  grid-row: 1 / -1;
  align-self: stretch;
}

.product-features__item-box h3 {
  margin-bottom: 5px;
  font-size: 24px;
  font-weight: 400;
}

.video {
  width: 280px;
  height: 150px;
  background-image: url("product-features2.2d0a4ace.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  grid-column: 2 / 3;
  justify-content: end;
  align-items: flex-start;
  padding: 9px 10px;
  display: flex;
}

.video__button {
  margin-left: 22px;
}

.video span {
  color: var(--white-text-color);
  font-size: 13px;
}

.games {
  margin-bottom: 100px;
}

.games__title {
  margin-bottom: 50px;
}

.games__inner {
  grid-template-columns: 1fr 3fr;
  display: grid;
}

.games__descr {
  padding-right: 10px;
}

.games__slogan {
  text-transform: uppercase;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 10px;
  font-size: 36px;
  line-height: 1.15;
}

.games__text {
  margin-bottom: 30px;
}

.games__descr-link {
  text-transform: uppercase;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 1px solid var(--accent-color);
  padding: 16px 47px;
  font-size: 16px;
  font-weight: 700;
  display: inline-block;
}

.games__list {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.games__item {
  border: 1px solid var(--accent-color);
  flex-direction: column;
  padding: 20px;
  display: flex;
}

.games__img {
  margin-bottom: 10px;
}

.games__name {
  font-style: 24px;
  margin-bottom: 10px;
}

.games__text {
  margin-bottom: 30px;
}

.games__link {
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: auto;
}

.promo {
  max-width: 1800px;
  text-align: center;
  margin: 0 auto 110px;
  position: relative;
  overflow-x: hidden;
}

.promo__bg {
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
  text-transform: uppercase;
  opacity: .1;
  z-index: -1;
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 360px;
  font-weight: 700;
  line-height: .9;
  animation: line-text 7s linear 1s infinite;
  position: absolute;
}

@keyframes line-text {
  from {
    transform-origin: 0;
    transform: translateX(120%);
  }

  to {
    transform-origin: 100%;
    transform: translateX(-120%);
  }
}

.promo__image {
  margin-bottom: 30px;
  display: inline-block;
}

.promo .buy-button {
  margin: 0 auto;
}

